"use client";
import AddStudent from "@/components/AddStudent";
import DeleteStudents from "@/components/DeleteStudents";
import EditStudents from "@/components/EditStudents";
import useGlobalStore from "@/store/my-store";
import { Button, Switch, Table } from "antd";
import { useState } from "react";

function OrdersPage() {
  const state = useGlobalStore();

  const [AddOpen, setAddOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [EditStudent, setEditStudent] = useState({});
  const [deleteStudent, setDeleteStudent] = useState({});

  const showDrawer = () => {
    setAddOpen(true);
  };
  const showDrawerEdit = () => {
    setEditOpen(true);
  };

  const onClose = () => {
    setAddOpen(false);
  };
  const onCloseEdit = () => {
    setEditOpen(false);
  };

  return (
    <div className="flex flex-col items-center">
      <div className="flex gap-2 items-center justify-center  ">
        <Button>Umumiy son: {state.orders.length}</Button>
        <Button>
          Faollar:
          {
            state.orders.filter((item: any) => {
              return item.active;
            }).length
          }
        </Button>
        <Button>
          Nofaollar:{" "}
          {state.orders.reduce((count: any, item: any) => {
            return !item.active ? count + 1 : count;
          }, 0)}
        </Button>
      </div>
      <div className="flex flex-col my-5">
        <AddStudent onClose={onClose} open={AddOpen} showDrawer={showDrawer} />
        <EditStudents
          editOpen={editOpen}
          showDrawerEdit={showDrawerEdit}
          onCloseEdit={onCloseEdit}
          EditStudent={EditStudent}
          setEditStudent={setEditStudent}
        />
        <Table
          columns={[
            {
              title: "ID",
              dataIndex: "id",
              render: (id: any, student, s) => {
                return (
                  <span
                    className=" cursor-pointer "
                    onClick={() => {
                      setEditOpen(true);
                      setEditStudent(student);
                    }}
                  >
                    {id}
                  </span>
                );
              },
            },
            {
              title: "Nomi",
              dataIndex: "name",
            },
            {
              title: "Narx",
              dataIndex: "total_price",
            },
            {
              title: "Manzil",
              dataIndex: "address",
            },
            {
              title: "Mijoz",
              dataIndex: "studentID",
              render: (group_id) => {
                const group = state.students.find((f) => {
                  return f.id === group_id;
                });
                return group?.firstName;
              },
            },
            {
              title: "Kategoriyasi",
              dataIndex: "productID",
              render: (group_id) => {
                const group = state.products.find((f) => {
                  return f.id === group_id;
                });
                return group?.name;
              },
            },
          ]}
          dataSource={state.orders.map((i: any) => {
            return {
              ...i,
              key: i.id,
            };
          })}
        />
        <Button
          color="danger"
          type="text"
          variant="text"
          onClick={() => {
            if (window.confirm("Hammasini o'chirishni tasdiqlaysizmi?")) {
              localStorage.removeItem("orders");
              alert("Hammasi o'chirildi!");
            }
          }}
        >
          Hammasini ochirish{" "}
        </Button>
      </div>
    </div>
  );
}

export default OrdersPage;
